@import "./sass-variables.sass";

@mixin homeItem {
    width: 230px;
    height: 230px;
    font-size: 30px;
    margin: 20px;
    display: flex;

    span {
        margin: auto; } }

.homeItems {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 20px;

    a {
        text-decoration: none; }

    .homeLink {
        @include homeItem;
        background: $cmain;
        color: $cdark;
        cursor: pointer;
        transition: 1s;
        box-shadow: $smain;

        &:hover {
            background: $cdarkgradient;
            color: $cmain;
 } }            // box-shadow: $ssecond

    .homeTitle {
        @include homeItem;
        color: $clight;
        background: $cdarkgradient;
        font-size: 20px;
 } }        // box-shadow: $smain

