@import "./sass-variables.sass";

.headerComponent {
    position: relative;
    height: 504px;
    width: 100vw;
    background-image: URL('https://cdn.pixabay.com/photo/2018/12/21/20/17/music-3888684_960_720.png');
    background-size: 75%;
    background-position: 180% 30%;
    display: flex;
    box-shadow: $smain;

    .homeButton {
        position: fixed;
        display: flex;
        width: 50px;
        height: 50px;
        background-color: $cmain;
        justify-content: center;
        align-items: center;
        // margin: 25px 5px 15px 25px
        left: calc(10vw + 20px);
        top: 40px;
        z-index: 15;
        transition: 0.3s;

        i {
            text-decoration: none;
            color: $cdark;
            font-size: 40px; }

        &:hover {
            background-color: $cdark;
            i {
                color: $cmain; } } }

    .hamburger {
        padding: 10px;
        cursor: pointer;
        display: inline-block;
        border: 0;
        margin: 0;
        z-index: 10;
        position: fixed;
        background-color: $cmain;
        transform: translateX(calc(90vw - 80px));
        top: 30px;
        outline: none;
        transition: transform 0.1s ease-in-out;
        &:hover {
 }            // background-color: $chover

        .hamburgerBox {
            position: relative;
            width: 35px;
            height: 35px;
            display: inline-block;

            &:hover {}

            .hamburgerInner {
                width: 100%;
                height: 5px;
                background-color: $cdark;
                position: absolute;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
                transition: background-color .1s .1s ease-in-out; }
            .hamburgerInnerHover {
                width: 100%;
                height: 5px;
                background-color: $clight;
                position: absolute;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
                transition: background-color .1s .1s ease-in-out; }

            .hamburgerInner::before,
            .hamburgerInner::after,
            .hamburgerInnerHover::before,
            .hamburgerInnerHover::after {
                width: 100%;
                height: 5px;
                background-color: black;
                position: absolute;
                left: 0;
                content: '';
                transition: transform .1s .1s ease-in-out; }

            .hamburgerInner::before,
            .hamburgerInnerHover::before {
                top: -10px; }

            .hamburgerInner::after,
            .hamburgerInnerHover::after {
                top: 10px; } } } }

.hamburgerActive {
    transform: translateX(88.5vw) !important;
    background-color: transparent !important;

    .hamburgerInner {
        background-color: transparent !important; } }

.hamburgerActive .hamburgerInner:before {
    transform: translateY(10px) rotate(45deg); }

.hamburgerActive .hamburgerInner:after {
    transform: translateY(-10px) rotate(-45deg); }

.hamburgerDisabledHover {
    .hamburgerInner {
        background-color: white; }
    .hamburgerInner:before,
    .hamburgerInnerHover:before {
        transform: translateY(-5px); }
    .hamburgerInner:after,
    .hamburgerInnerHover:after {
        transform: translateY(5px); } }

