//     .hamburgerInner
//         background-color: white

.sidebar {
    background-color: $clight;
    position: fixed;
    min-height: 100vh;
    z-index: 5;
    width: 50vw;
    min-width: 500px;
    padding-top: 100px;
    transform: translateX(100vw);
    transition: transform 0.2s ease-in-out;

    nav {
        height: 400px;
        display: flex;
        color: $cdark;
        justify-content: space-around;

        a {
            text-decoration: none;
            color: $cdark;
            transition: 0.2s;

            &:hover {
                color: $cmain;
                // text-shadow: .5px .5px $cdark
                transform: translateX(20px); } }

        ul {
            list-style: none;
            display: flex;
            flex-direction: column;
            justify-content: space-between; }

        .navList {
            align-items: flex-start;
            padding: 10px;

            li {
                padding: 10px;
                font-size: 25px; } }

        .socialList {
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            a {
                text-decoration: none;
                color: $cdark;
                font-size: 40px;
                text-align: center;
                padding: 10px;
                background-color: transparent;
                cursor: pointer;
                border: none;
                outline: none;
                transform: rotate(0deg); }
            a:first-child {
                &:hover {
                    color: #FF0000;
                    // transform: rotate(360deg)
 } }                    // text-shadow: .2px .2px $cdark
            a:nth-child(2) {
                &:hover {
                    color: #4267B2; } }
            a:nth-child(3) {
                &:hover {
                    color: #833AB4; } }
            a:nth-child(4) {
                &:hover {
                    color: #1DA1F2; } } } } }

.sidebarActive {
    transform: translateX(50vw) !important; }

.logo {
    position: absolute;
    z-index: 5 !important;
    height: 40%;
    bottom: 0;
    left: calc(10% + 20px);

    img {
        height: 60%; } }

